<template>
    <div class="my-tab-content">
        <el-tabs v-model="tabActive">
            <el-tab-pane label="流量采集" name="config">
                <el-collapse v-model="activeNames">
                    <el-collapse-item name="1">
                        <template slot="title">
                            采集规则
                        </template>
                        <flow-record-config @get-flow-export="getFlowExport"></flow-record-config>
                    </el-collapse-item>
                    <el-collapse-item title="采集结果" name="2">
                        <template slot="title">
                            采集结果 -- 规则ID：<span style="color: #32cd32;font-weight: bold;">{{flowRecordExportParams.configId}}</span>
                        </template>
                        <flow-record-export :flow-record-export-params="flowRecordExportParams"></flow-record-export>
                    </el-collapse-item>
                </el-collapse>
            </el-tab-pane>
            <el-tab-pane label="流量重放" name="replay">
                <flow-record-replay></flow-record-replay>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import FlowRecordReplay from './flow_record_replay/flow_record_replay';
import FlowRecordExport from 'openSource/index_detail/flow_record/flow_record_config/flow_record_export';
import FlowRecordConfig from 'openSource/index_detail/flow_record/flow_record_config/flow_record_config';

export default {
  components: {
    'flow-record-replay': FlowRecordReplay,
    'flow-record-export': FlowRecordExport,
    'flow-record-config': FlowRecordConfig,
  },
  data() {
    return {
      tabActive: 'config',
      activeNames: ['1', '2'],
      flowRecordExportParams: {},
    };
  },
  methods: {
    getFlowExport(params) {
      this.flowRecordExportParams = JSON.parse(JSON.stringify(params));
    },
  },
};
</script>



