<template>
    <div class="navbar">
        <div class="navbar-title">
            <span>
                <img src="../../image/pallas.png"/>
                Pallas一站式检索平台
            </span>
        </div>

        <div class="navbar-content">
            <el-menu theme="dark" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" :router="true">
                <template v-for='item in serviceList'>
                  <el-menu-item :index="item.index" :route='item' :key="item.index"><i :class="item.icon"></i>{{item.title}}</el-menu-item>
                </template>
                <el-submenu index="3" class="pull-right">
                    <template slot="title"><i class="fa fa-user"></i>{{loginUser || 'null'}}</template>
                    <el-menu-item index=""><a style="display: block;" href="/pallas/logout"><i class="fa fa-sign-out"></i>注销</a></el-menu-item>
                    <el-menu-item index=""><a style="display: block;" href="http://vask.vip.vip.com/post/add?lables=Pallas" target="_blank"><i class="fa fa-question-circle"></i>需求收集</a></el-menu-item>
                </el-submenu>
                <div class="header-item pull-right">
                    <el-dropdown>
                        <el-button type="text">
                            <span>{{currentEnv}}</span>
                            <i class="fa fa-ellipsis-v"></i>
                        </el-button>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for="item in envs" :key="item.name">
                                <a
                                    :href="item.onClick ? 'javascript:;' : `//${item.host}/`"
                                    class="el-button el-button--text env-link"
                                    :class="{ 'env-link-active': currentEnv === item.name }"
                                    @click="item.onClick ? item.onClick() : (() => {})"
                                >
                                    <span>{{item.name}}</span>
                                    <div>{{item.host}}</div>
                                </a>
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </el-menu>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      serviceList: [
        { index: '/index', title: '索引管理', icon: 'fa fa-search', path: this.$routermapper.GetPath('indexManage') },
        { index: '/cluster', title: 'ES集群管理', icon: 'fa fa-sitemap', path: this.$routermapper.GetPath('clusterManage') },
        { index: '/authority', title: '模板变更', icon: 'fa fa-check-square-o', path: this.$routermapper.GetPath('authorityManage') },
        { index: '/agent', title: '代理管理', icon: 'fa fa-arrows', path: this.$routermapper.GetPath('agentManage') },
        { index: '/token', title: 'Token管理', icon: 'fa fa-key', path: this.$routermapper.GetPath('tokenManage') },
        { index: '/plugin', title: '插件管理', icon: 'fa fa-puzzle-piece', path: this.$routermapper.GetPath('pluginManage') },
      ],
      pluginList: [
        { index: '/plugin', title: '插件状态', icon: 'fa fa-dot-circle-o', path: this.$routermapper.GetPath('pluginManage') },
        { index: '/plugin_upgrade', title: '插件升级', icon: 'fa fa-upload', path: this.$routermapper.GetPath('pluginUpgrade') },
      ],
      envs: [
        { name: 'Dev', host: 'pallas-dev.vip.vip.com' },
        { name: 'QA', host: 'pallas-qa.vip.vip.com' },
        { name: 'Staging', host: 'pallas-staging.vip.vip.com' },
        { name: 'Production', host: 'pallas.vip.vip.com' },
      ],
    };
  },
  computed: {
    activeIndex() {
      return this.$route.path.split('_')[0];
    },
    loginUser() {
      return this.$store.state.loginUser;
    },
    currentEnv() {
      if (location.host === 'pallas-dev.vip.vip.com') {
        return 'Dev';
      }
      console.warn(location.host);
      const env = this.envs.find(e => e.host === location.host);
      if (env) {
        return env.name;
      }
      return 'localhost';
    },
  },
};

</script>

<style type="text/css">
.navbar {
    width: 100%;
    display: table;
}

.navbar-title {
    display: table-cell;
    background-color: #373a3c;
    color: white;
    height: 60px;
    line-height: 60px;
    font-weight: 700;
    width: 260px;
}

.navbar-title span img {
    float:left;
    padding: 10px 10px 0px 25px;
}

.navbar-content {
    display: table-cell;
    vertical-align: middle;
}

.navbar-content ul {
    background-color: #373a3c;
    border-radius: 0;
}

.logout-a-link {
    text-decoration: initial;
}

.header-item {
    height: 60px;
    padding: 14px 0;
    box-sizing: border-box;
}
.header-item .el-button--text {
    color: #bfcbd9;
}
.header-item .el-button--text:hover, .header-item .el-button--text:focus {
    color: #fff;
}
</style>
<style scoped>
.env-link {
  text-align: left;
}
.env-link:hover {
    text-decoration: none;
}
.env-link span {
    color: #eee;
}
.env-link div {
    margin-top: 5px;
    color: #A9A9A9;
}
.env-link-active span, .env-link-active div{
    color: #fff;
}
</style>



