import array from '../Pallas-Web/pallas-console-web/src/utils/array';
import http from '../Pallas-Web/pallas-console-web/src/utils/request';
import message from '../Pallas-Web/pallas-console-web/src/utils/message';
import validate from '../Pallas-Web/pallas-console-web/src/utils/validate';
import option from './option';
import routerMapper from './router_mapper';

const Utils = {
  array,
  http,
  message,
  validate,
  option,
  routerMapper,
};
/* eslint-disable no-param-reassign */
Utils.install = (Vue) => {
  Vue.prototype.$array = array;
  Vue.prototype.$http = http;
  Vue.prototype.$message = message;
  Vue.prototype.$validate = validate;
  Vue.prototype.$option = option;
  Vue.prototype.$routermapper = routerMapper;
};
export default Utils;



