<template>
    <div>
        <div :id="id" :options="options" style="width: 100%;height:200px;"></div>
    </div>
</template>

<script>
import Highcharts from 'highcharts';
import Exporting from 'highcharts/modules/exporting';
import DarkThemes from 'highcharts/themes/dark-unica';

Exporting(Highcharts);
DarkThemes(Highcharts);

export default {
  props: ['id', 'optionInfo'],
  data() {
    return {
      options: {
        chart: {
          type: 'column',
          marginTop: 25,
        },
        credits: {
          enabled: false,
        },
        title: {
          text: null,
        },
        xAxis: {
          categories: this.optionInfo.xCategories,
          labels: {
            formatter() {
              return Highcharts.dateFormat('%m/%d/%H:00', this.value + (8 * 60 * 60 * 1000));
            },
          },
        },
        yAxis: {
          min: 0,
          title: {
            text: this.optionInfo.yTitle,
          },
          stackLabels: {
            enabled: true,
            style: {
              fontWeight: 'bold',
              color: (Highcharts.theme && Highcharts.theme.textColor) || 'gray',
            },
          },
        },
        tooltip: {
          shared: true,
          useHTML: true,
          headerFormat: '',
        },
        legend: {
          layout: 'vertical',
          align: 'right',
          verticalAlign: 'top',
          x: 10,
          y: 115,
          floating: true,
          borderWidth: 1,
          backgroundColor: ((Highcharts.theme && Highcharts.theme.legendBackgroundColor) || '#FFFFFF'),
          shadow: true,
        },
        plotOptions: {
          series: {
            borderWidth: 0,
            dataLabels: {
              enabled: true,
              format: '{point.y}',
            },
          },
          column: {
            dataLabels: {
              enabled: false,
              color: (Highcharts.theme && Highcharts.theme.dataLabelsColor) || 'white',
              style: {
                textShadow: '0 0 3px black',
              },
            },
          },
        },
        series: this.optionInfo.seriesData,
      },
    };
  },
  watch: {
    optionInfo: 'buildPage',
  },
  methods: {
    buildPage() {
      if (this.optionInfo) {
        this.options.xAxis.categories = this.optionInfo.xCategories;
        this.options.series = this.optionInfo.seriesData;
        this.options.yAxis.text = this.optionInfo.yTitle;
        Highcharts.chart(this.id, this.options);
      }
    },
  },
  mounted() {
    Highcharts.chart(this.id, this.options);
  },
};
</script>



