<template>
    <div>
        <template v-if="initialized">
            <Container></Container>
        </template>
        <template v-else>
            <div v-loading.fullscreen="loading" element-loading-text="Pallas正在初始化应用，请稍等···"></div>
        </template>
    </div>
</template>
<script>
import { SET_LOGIN_USER } from './Pallas-Web/pallas-console-web/src/store/types';

export default {
  data() {
    return {
      loading: false,
      initialized: true,
      interval: 0,
    };
  },
  methods: {
    // 隔十分钟请求一次心跳
    heartbeat() {
      this.interval = setInterval(() => {
        this.$http.get('/heartbeat.json').then(() => {
        });
      }, 600000);
    },
    getLoginUser() {
      return this.$http.get('/system/loginUser.json').then((data) => {
        this.$store.dispatch(SET_LOGIN_USER, data);
      });
    },
    init() {
      this.loading = true;
      Promise.all([this.getLoginUser()]).then(() => {
        this.heartbeat();
      })
      .finally(() => {
        this.initialized = true;
        this.loading = false;
      });
    },
  },
  created() {
    this.init();
  },
  destroyed() {
    clearInterval(this.interval);
  },
};
</script>



