<template>
    <div class="my-tab-content">
        <div class="content">
            <el-button type="primary" icon="plus" @click="handleAdd" :disabled="!isAllPrivilege">新增流量重放</el-button>
        </div>
        <div class="content">
            <template>
                <el-table :data="recordReplayList" border style="width: 100%" v-loading="loading" element-loading-text="请稍等···">
                    <el-table-column label="重放id" prop="id" width="70px"></el-table-column>
                    <el-table-column label="目标模板" prop="template.templateName">
                        <template scope="scope">{{formatTargetTemplate(scope.row.targetTemplateId)}}</template>
                    </el-table-column>
                    <el-table-column label="SaturnJob" prop="jobName"></el-table-column>
                    <el-table-column label="循环次数" prop="loop" width="80px"></el-table-column>
                    <el-table-column label="重放时长(s)" prop="duration" width="90px"></el-table-column>
                    <el-table-column label="重放分片数" prop="executorCount"></el-table-column>
                    <el-table-column label="分片线程" prop="threadCount"></el-table-column>
                    <el-table-column label="重放总线程数" prop="">
                        <template scope="scope">
                            {{scope.row.threadCount * scope.row.executorCount}}
                        </template>
                    </el-table-column>
                    <el-table-column label="重放连接数" prop="connectionCount"></el-table-column>
                    <el-table-column label="超时(ms)" prop="timeout" width="90px"></el-table-column>
                    <el-table-column label="QPS" prop="qps" width="70px"></el-table-column>
                    <el-table-column label="最后更新时间" prop="endTime" width="155px">
                        <template scope="scope">{{scope.row.updateTime | formatDate}}</template>
                    </el-table-column>
                    <el-table-column label="状态" prop="state" width="80px">
                        <template scope="scope">
                            <el-tooltip v-if="scope.row.state === 4" :content="scope.row.note" placement="bottom-start">
                                <el-tag :type="'danger'">{{flowRecordStateMap[scope.row.state]}}</el-tag>
                            </el-tooltip>
                            <el-tag v-else :type="(scope.row.state !== 3 && scope.row.state !== 4) ? 'success' : 'danger'">{{flowRecordStateMap[scope.row.state]}}</el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作" width="80">
                        <template scope="scope">
                            <el-dropdown trigger="click">
                              <span class="el-dropdown-link">
                                操作<i class="el-icon-caret-bottom el-icon--right"></i>
                              </span>
                                <el-dropdown-menu class="dropdown-operation" slot="dropdown">
                                    <el-dropdown-item v-show="scope.row.state !== 1" v-if="isAllPrivilege"><a @click="startReplay(scope.row)"><span><i class="fa fa-hand-o-right"></i>执行</span></a></el-dropdown-item>
                                    <el-dropdown-item v-show="scope.row.state === 1" v-if="isAllPrivilege"><a @click="stopReplay(scope.row)"><span><i class="fa fa-stop-circle-o"></i>停止</span></a></el-dropdown-item>
                                    <el-dropdown-item v-show="scope.row.state !== 1" v-if="isAllPrivilege"><a @click="handleEdit(scope.row, 'edit')"><span><i class="fa fa-pencil-square-o"></i>编辑</span></a></el-dropdown-item>
                                    <el-dropdown-item v-show="scope.row.state !== 1" v-if="isAllPrivilege"><a @click="handleDelete(scope.row)"><span><i class="fa fa-trash"></i>删除</span></a></el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
            <div class="my-pagination" v-if="versionData.total != 0">
                <el-pagination layout="prev, pager, next" :total="total" :page-size="pageSize" :current-page="currentPage" @current-change="changePage"></el-pagination>
            </div>
            <div v-if="isReplayInfoVisible">
                <flow-replay-dialog :replay-operation="replayOperation" :replay-info-title="replayInfoTitle" :replay-info="replayInfo" :records="records" :templates="templates" :saturnJobs="saturnJobs" @close-dialog="closeDialog" @operate-close-dialog="operateCloseDialog"></flow-replay-dialog>
            </div>
        </div>
    </div>
</template>

<script>
  import FlowReplayDialog from './flow_replay_dialog';

  export default {
    data() {
      return {
        loading: false,
        activeName: 'first',
        recordReplayList: [],
        indexId: this.$route.query.indexId,
        indexName: this.$route.query.indexName,
        isMetaDataNull: false,
        pageSize: 10,
        replayOperation: '',
        replayInfoTitle: '',
        total: 0,
        currentPage: 1,
        versionData: {},
        replayInfo: {},
        templates: [],
        records: [],
        saturnJobs: [
          {
            id: 'FlowReplayJobA',
          }, {
            id: 'FlowReplayJobB',
          }],
        isReplayInfoVisible: false,
        isAllPrivilege: false,
        flowRecordStateMap: {
          0: '就绪',
          1: '正在重放',
          2: '已完成',
          3: '已终止',
          4: '重放异常',
        },
      };
    },
    methods: {
      init() {
        this.getRecordReplayList(1);
        this.getTemplateByIndex();
      },
      formatTargetTemplate(target) {
        let result = '';
        this.templates.forEach((item) => {
          if (item.id === target) {
            result = item.templateName;
          }
        });
        return result;
      },
      changePage(currentPage) {
        this.getRecordReplayList(currentPage);
      },
      getTemplateByIndex() {
        const params = {
          indexId: this.$route.query.indexId,
        };
        this.$http.get(`/record/index_template/list.json?indexId=${params.indexId}`).then((data) => {
          this.templates = data;
        });
      },
      handleAdd() {
        this.loading = true;
        this.$http.get(`/record/flow_record/list.json?doneOnly=true&indexId=${this.$route.query.indexId}`).then((data) => {
          this.isReplayInfoVisible = true;
          this.replayInfoTitle = '新增流量重放';
          this.replayOperation = 'add';
          this.records = data;
          const replayAddInfo = {
            id: null,
            targetTemplateId: '',
            indexId: this.$route.query.indexId,
            jobName: '-',
            recordId: '',
            threadCount: 2,
            token: '',
            loop: 1,
            duration: 120,
            connectionCount: 2,
            usedLocalTemplate: false,
            timeout: 200,
            qps: 50,
            note: '',
          };
          this.replayInfo = JSON.parse(JSON.stringify(replayAddInfo));
        })
        .finally(() => {
          this.loading = false;
        });
      },
      handleDelete(row) {
        this.$message.confirmMessage(`确定删除流量重放作业${row.id}吗?`, () => {
          this.loading = true;
          this.$http.post('/record/flow_replay/delete.json', { indexId: this.indexId, id: row.id }).then(() => {
            this.$message.successMessage('删除成功', () => {
              this.getRecordReplayList(1);
            });
          })
          .finally(() => {
            this.loading = false;
          });
        });
      },
      handleEdit(row) {
        this.loading = true;
        this.$http.get(`/record/flow_record/list.json?doneOnly=true&indexId=${this.$route.query.indexId}`).then((data) => {
          this.records = data;
          this.isReplayInfoVisible = true;
          this.replayInfoTitle = '编辑流量重放';
          this.replayOperation = 'edit';
          this.replayInfo = row;
          this.replayInfo.indexId = this.$route.query.indexId;
        })
        .finally(() => {
          this.loading = false;
        });
      },
      startReplay(row) {
        this.$message.confirmMessage(`确定要开始重放${row.id}吗?`, () => {
          this.loading = true;
          this.$http.post('/record/flow_replay/start.json', { indexId: this.indexId, id: row.id }).then(() => {
            this.$message.successMessage('流量重放激活', () => {
              this.getRecordReplayList(1);
            });
          })
          .finally(() => {
            this.loading = false;
          });
        });
      },
      stopReplay(row) {
        this.$message.confirmMessage(`立刻停止重放${row.id}吗?`, () => {
          this.loading = true;
          this.$http.post('/record/flow_replay/stop.json', { indexId: this.indexId, id: row.id }).then(() => {
            this.$message.successMessage('禁用规则成功', () => {
              this.getRecordReplayList(1);
            });
          })
          .finally(() => {
            this.loading = false;
          });
        });
      },
      getRecordReplayList(_currentPage) {
        const params = {
          currentPage: _currentPage,
          pageSize: this.pageSize,
          indexId: this.$route.query.indexId || '',
        };
        this.loading = true;
        this.$http.get('/record/flow_replay/page.json', params).then((data) => {
          this.recordReplayList = data.list;
          this.total = data.total;
          this.isAllPrivilege = data.allPrivilege;
        })
        .finally(() => {
          this.loading = false;
        });
      },
      closeDialog() {
        this.isReplayInfoVisible = false;
      },
      operateCloseDialog() {
        this.isReplayInfoVisible = false;
        this.getRecordReplayList(1);
      },
    },
    filters: {
      translateIsEnable(data) {
        const IS_ENABLE = { true: '已启用', false: '待启用' };
        return IS_ENABLE[data];
      },
    },
    components: {
      'flow-replay-dialog': FlowReplayDialog,
    },
    created() {
      this.init();
    },
    watch: {
      $route: 'getRecordReplayList',
    },
  };

</script>



