<template>
    <el-dialog :title="replayInfoTitle" size="small" v-model="isreplayInfoVisible" :before-close="closeDialog" v-loading="loading" element-loading-text="请稍等···">
        <el-form :model="replayInfo" :rules="rules" ref="replayInfo" label-width="140px">
            <el-row>
                <el-col :span="16">
                    <el-form-item label="目标模板" prop="targetTemplateId" required>
                        <el-select v-model="replayInfo.targetTemplateId" style="width:100%;">
                            <el-option v-for="item in templates" :label="item.templateName" :value="item.id" :key="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="6" style="padding-left: 10px">
                    <el-checkbox v-model="replayInfo.usedLocalTemplate">使用本地模板</el-checkbox>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="Record Id" prop="recordId" required>
                        <el-select v-model="replayInfo.recordId" style="width:100%;">
                            <el-option v-for="item in records" :label="item.id" :value="item.id" :key="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="Token" prop="token" required>
                        <el-input v-model="replayInfo.token"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="循环次数" prop="loop" required>
                        <el-input v-model="replayInfo.loop"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="1">
                <span class="warning">
                        <el-tooltip class="item" effect="dark" placement="bottom">
                            <div slot="content">循环次数为每个分片的每个重放线程的循环次数<br/>最终循环次数=循环次数 * 线程数 * 分片数</div>
                            <i class="fa fa-info-circle"></i>
                        </el-tooltip>
                    </span>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="重放时长（秒）" prop="duration" required>
                        <el-input v-model="replayInfo.duration"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="重放线程(单分片)" prop="threadCount" required>
                        <el-input v-model="replayInfo.threadCount"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="重放最大连接数" prop="connectionCount" required>
                        <el-input v-model="replayInfo.connectionCount"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="超时设置" prop="timeout" required>
                        <el-input v-model="replayInfo.timeout"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="总QPS" prop="qps" required>
                        <el-input v-model="replayInfo.qps"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

        </el-form>
        <div slot="footer" class="dialog-footer">
           <el-button @click="closeDialog()">取消</el-button>
           <el-button type="confirm" @click="submitreplayInfo()">保存</el-button>
        </div>
    </el-dialog>
</template>

<script>
export default {
  props: ['replayOperation', 'replayInfo', 'replayInfoTitle', 'allPhysicals', 'records', 'templates', 'saturnJobs'],
  data() {
    return {
      loading: false,
      isreplayInfoVisible: true,
      rules: {
        targetTemplateId: [{ validator: this.$validate.validateSelect, trigger: 'change' }],
        startTime: [{ validator: this.$validate.validateTime, trigger: 'change' }],
        endTime: [{ validator: this.$validate.validateTimeExpire, trigger: 'change' }],
        sampleRate: [{ required: true, message: '抽样系数不能为空', trigger: 'blur' }],
        limit: [{ required: true, message: '采集数量不能为空', trigger: 'blur' }],
      },
    };
  },
  methods: {
    submitreplayInfo() {
      this.$refs.replayInfo.validate((valid) => {
        if (valid) {
          const invalid = this.templates.some((item) => {
            if (item.id === this.replayInfo.targetTemplateId) {
              if (item.id === -1 && this.replayInfo.usedLocalTemplate) {
                this.$message.errorMessage('全部模板重放不支持使用本地模板');
                return true;
              } else if (item.id !== -1 && item.hisCount === 0
                && !this.replayInfo.usedLocalTemplate) {
                this.$message.errorMessage('目标模板在ES不存在，请先提交审批，或使用本地模板');
                return true;
              }
            }
            return false;
          });
          if (invalid) {
            return;
          }
          if (this.replayOperation === 'add') {
            this.replayRequest('/record/flow_replay/add.json');
          } else {
            this.replayRequest('/record/flow_replay/add.json');
          }
        }
      });
    },
    replayRequest(url) {
      this.loading = true;
      this.$http.post(url, this.replayInfo).then((data) => {
        this.$message.successMessage('保存成功', () => {
          this.$emit('operate-close-dialog');
        });
      })
      .finally(() => {
        this.loading = false;
      });
    },
    closeDialog() {
      this.$emit('close-dialog');
    },
  },
  computed: {
    isEditable() {
      return this.replayOperation === 'edit';
    },
  },
};

</script>
<style scoped>
    .warning{
        margin: 10px 5px;
        padding-left: 3px;
        padding-top: 10px;
        color: #c8c8ca;
    }
</style>



