<template>
    <el-dialog :title="syncTitle" v-model="isStartSyncVisible" :before-close="closeDialog">
        <el-form :model="startSyncInfo" :rules="rules" ref="startSyncInfo" label-width="100px">
            <div class="sync-content">
                <el-row :gutter="20">
                    <el-col :span="11">
                        <el-form-item label="ES Bulk" prop="bulk">
                            <el-input v-model.number="startSyncInfo.bulk"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="11">
                        <el-form-item label="ES thread" prop="thread">
                            <el-input v-model.number="startSyncInfo.thread"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="20">
                    <el-col :span="11">
                        <el-form-item label="DB Limit" prop="limit">
                            <el-input v-model.number="startSyncInfo.limit"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="11">
                        <el-form-item label="任务分割" prop="slice">
                            <el-input v-model.number="startSyncInfo.slice"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </div>
        </el-form>
        <div slot="footer" class="dialog-footer">    
                <el-button @click="closeDialog()">取消</el-button>
                <el-button type="confirm" @click="submitSyncInfo()">确定</el-button>
        </div>
    </el-dialog>
</template>

<script>

export default {
  props: ['startSyncInfo'],
  data() {
    return {
      isStartSyncVisible: true,
      rules: {
        bulk: [{ required: true, message: 'ES Bulk不能为空' }, { type: 'number', message: 'ES Bulk值必须为数字值' }],
        thread: [{ required: true, message: 'ES thread不能为空' }, { type: 'number', message: 'ES thread值必须为数字值' }],
        limit: [{ required: true, message: 'DB Limit不能为空' }, { type: 'number', message: 'DB Limit值必须为数字值' }],
        slice: [{ required: true, message: '任务分割不能为空' }, { type: 'number', message: '任务分割值必须为数字值' }],
      },
    };
  },
  methods: {
    submitSyncInfo() {
      this.$refs.startSyncInfo.validate((valid) => {
        if (valid) {
          this.$emit('sync-success');
        } else {
          console.log('error submit!!');
        }
      });
    },
    closeDialog() {
      this.$emit('close-sync-dialog');
      this.$refs.startSyncInfo.resetFields();
    },
  },
  computed: {
    syncTitle() {
      const title = `开始同步${this.startSyncInfo.versionId}`;
      return title;
    },
  },
};

</script>

<style type="text/css">
.sync-content {
    margin-bottom: 10px;
}
</style>



