import Vue from 'vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-default/index.css';
import 'font-awesome/css/font-awesome.css';
import './Pallas-Web/pallas-console-web/src/components';
import './components';
import './Pallas-Web/pallas-console-web/src/directives/';
import './Pallas-Web/pallas-console-web/src/components/filter';
import App from './App';
import Store from './Pallas-Web/pallas-console-web/src/store';
import router from './routers';
import Utils from './utils';
import './Pallas-Web/pallas-console-web/src/styles/main.css';
import './Pallas-Web/pallas-console-web/src/styles/element.css';

Vue.use(ElementUI);
Vue.use(Utils);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store: Store,
  router,
  render: h => h(App),
});



